.form {
  font-size: 14px;
    font-weight: 400 ;
    line-height: 20px;
    color: #292929;
}

textarea {
  &::placeholder {
    @include placeholder;
  }
}

input {
  box-sizing: border-box;

&::placeholder {
  @include placeholder;

}
}

.contacts {
  @include pageGrid;
  gap: 48px;

  &__title {
    justify-self: center;
    grid-column: 1 / -1;
    margin-bottom: 0;

    @include onTablet {
      grid-column: 2 / -2;
    }

    @include onDesktop {
      grid-column: span 5 / -5;
    }
  }

  &__form {
    width: 100%;
    grid-column: 1 / -1;

    @include onTablet {
      grid-column: span 3;
    }

    @include onDesktop {
      grid-column: span 5;
    }

    &--name {
      @include button-forms;
      margin-bottom: 16px;
      padding-inline: 16px;
    }

    &--email {
      @include button-forms;
      margin-bottom: 16px;
      padding-inline: 16px;
    }

    &--message {
      box-sizing: border-box;
      @include button-forms;
      grid-column: 1 / 6;
      line-height: 20px;
      border-radius: 16px;
      margin-bottom: 32px;
      resize: none;
      white-space: pre-wrap;
      min-height: 120px;
      overflow: hidden;
      padding-left: 16px;
    }

    &--submit {
      width: 100%;
      @include button;
      @include cursor;

      @include onTablet {
        grid-column: span 3;
      }

      @include onDesktop {
        grid-column: span 5;
      }
    }
  }

  &__blocks {
    grid-column: 1 / -1;

    @include onTablet {
      grid-column: 4 / 7;
    }

    @include onDesktop {
      grid-column: -6 / -3;
    }
  }

  &__block {
    &--text {
      margin: 0;
      margin-bottom: 8px;
      font-size: 14px;
      font-weight: 400;
    }

    &--text1 {
      margin: 0;
      font-size: 16px;
      font-weight: 500;
      margin-bottom: 24px;
    }
  }
}
