.product {
  @include hover(transform, scale(1.07));

  &__photo {
    width: 100%;
    margin-bottom: 32px;
  }

  &__title {
    margin: 0;
    margin-bottom: 8px;

    @include titleProduct;
  }

  &__description {
    margin: 0;
    margin-bottom: 16px;
  }

  &__price {
    margin: 0;
    @include titleProduct;
  }
}
